*{
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    height: 100%;
    background-color: #eff3f7;
}

html, body, #root, .container{
    height:100%;
}

.loader{
    width:100%;
    height:100%;
    display:flex;
    justify-content:center;
    align-items:center;
}

.movies{
    display:flex;
    justify-content:space-between;
    flex-wrap:wrap;
    padding: 50px;
    width:80%;
}
